* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: system-ui, sans-serif;
  background: #020617;
  color: #e5e7eb;
}

/* center app */
.app {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* counter card */
.counter-box {
  background: #020617;
  border: 1px solid #1f2937;
  border-radius: 18px;
  padding: 24px 28px 28px;
  text-align: center;
  width: 280px;
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.6);
}

.counter-box h1 {
  font-size: 1.3rem;
  margin-bottom: 14px;
}

.count {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 16px;
  color: #22c55e;
}

.buttons {
  display: flex;
  gap: 10px;
  justify-content: center;
}

button {
  width: 60px;
  height: 38px;
  border-radius: 999px;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  background: #3b82f6;
  color: #e5e7eb;
}

button:hover {
  background: #2563eb;
}

button:disabled {
  background: #374151;
  cursor: not-allowed;
}

.hint {
  margin-top: 10px;
  font-size: 0.8rem;
  color: #9ca3af;
}
